import { getEndpoint } from '@fe/partials-nuxt3'

import type { AuthProvider } from './auth'
import { createApi } from './base/api-wrapper'
import { BlanketService } from './services/blanket'

export const createServices = (authProvider?: AuthProvider) => {
	return {
		blanket: createApi(BlanketService, getEndpoint('blanket'), authProvider),
	}
}
