import type { AxiosInstance } from 'axios'
import { logger } from '@fe/bonsai-logger-nuxt3'

export const globalErrorHandler = (axios: AxiosInstance): void => {
	axios.interceptors.request.use((response) => response, (error) => {
		logger.errorObject.setErrorType(error, 'ServiceRequestError')
		return Promise.reject(error)
	})

	axios.interceptors.response.use((response) => response, (error) => {
		logger.errorObject.setErrorType(error, 'ServiceResponseError')
		return Promise.reject(error)
	})
}
