import { AuthType } from '@fe/nuxt3-config/shared-types'
import { envs } from '@fe/envs-nuxt3'

import { feNuxtConfig } from './fe-nuxt-config'

// eslint-disable-next-line import/no-unresolved
import cliJson from '~/cli.json'

interface EndpointInfo {
	service: string
	version?: string
	env: string
	namespace: string
}

interface EndpointOptions {
	useLbProxy?: boolean
}

const LB_PROXY_PREFIX = '/p-api/'

const env = envs.get('ENV')
const kuber_env = envs.get('KUBER_ENV')
const isLocal = env === 'local'
const apiConfig = cliJson.preset?.api ? Object.values(cliJson.preset.api) : []

function createTestruEndpoint({
	service,
	version = '',
	env,
	namespace,
	useLbProxy,
}: EndpointInfo & EndpointOptions): string {
	if (env === 'prod') {
		if (useLbProxy) {
			return `${LB_PROXY_PREFIX}${service}`
		}
		return `https://xxxx.apps.me/${service}.${namespace}`
	}

	return `https://${service}${version ? `-${version}` : ''}-${env}.apiurl.me`
}

function createO3Endpoint({
	service,
	version = '',
	env,
	useLbProxy,
}: EndpointInfo & EndpointOptions): string {
	const protocol = globalThis.location?.protocol

	if (env === 'prod') {
		if (useLbProxy) {
			return `${LB_PROXY_PREFIX}${service}`
		}
		// тут потенциально возможно обращение с http на https
		// но это не должно приводить к ошибкам (ошибки должны быть
		// только когда мы с защищенного ресурса загружаем уязвимый)
		return `https://${service}${version ? `-${version}` : ''}.t.tt.me`
	}

	if (protocol === 'https:') {
		// если зашли на dev или stg с https то в качестве backend используем
		// apiurl.me потому там "авто https"
		return `https://${service}${version ? `-${version}` : ''}-${env}.apiurl.me`
	}

	return `http://${service}${version ? `-${version}` : ''}.${env}.a.tt.me`
}

function getCreateEndpoint() {
	const isTestId = feNuxtConfig().authorization === AuthType.testId

	return isTestId ? createTestruEndpoint : createO3Endpoint
}

export function getEndpoint(name: string, useLbProxy = false) {
	const service = (apiConfig as EndpointInfo[]).find((item) => item.service === name)

	if (!service) {
		throw new Error(`getEndpoint function: can't find service with name ${name}`)
	}

	const apiProxyPort = envs.get('FE_API_PROXY_PORT')
	if (typeof process === 'object' && apiProxyPort) {
		return `http://0.0.0.0:${apiProxyPort}/${service.service}`
	}

	const createEndpoint = getCreateEndpoint()

	if (isLocal) {
		// если мы локально
		// мы используем env из cli.json
		return createEndpoint(service)
	}

	// в другом случае мы используем
	// KUBER_ENV из соответствующего values_*.yaml
	return createEndpoint({
		...service,
		version: '',
		env: kuber_env,
		useLbProxy,
	})
}
