import Axios from 'axios'
import type { AxiosInstance } from 'axios'
import { createDefaultHeaders } from '@fe/partials-nuxt3'

import type { AuthProvider } from '../auth'

import { globalErrorHandler } from './global-error-handler'
import { http401ErrorHandler } from './http-401-error-handler'
import { useBearerAuth } from './use-bearer-auth'

type ApiConstructor<T> = new (axios: AxiosInstance) => T

export const createApi = <T>(
	Api: ApiConstructor<T>,
	baseURL: string,
	authProvider?: AuthProvider,
): T => {
	const axios = Axios.create({
		timeout: 59000,
		baseURL,
		headers: createDefaultHeaders(),
	})

	if (authProvider) {
		useBearerAuth(axios, authProvider)
		http401ErrorHandler(axios, authProvider)
	}

	globalErrorHandler(axios)

	return new Api(axios)
}
