import { envs } from '@fe/envs-nuxt3'

function getMeshversionFromCookie() {
	const meshVersion = typeof document !== 'undefined' ? ('; ' + document?.cookie)?.split('; x-o3-meshversion=')?.pop()?.split(';')[0] : ''

	return meshVersion
}

// стоит держать в голове, что поидее названия
// заголовков должны быть не чувствительны к регистру,
// но поскольку "common practice to capitalize the first letter of every word"
// давайте придерживаться этого ниже
// https://myprogrammingnotes.com/http-headers-case-sensitive.html
export function createDefaultHeaders() {
	const mesh = getMeshversionFromCookie()

	return {
		'X-O3-App-Name': envs.get('SERVICE_NAME'),
		'X-O3-App-Version': envs.get('GIT_BRANCH'),
		...(mesh ? { 'X-O3-Meshversion': mesh } : {}),
	} as Record<string, string>
}
