import { logger } from '@fe/bonsai-logger-nuxt3'
import type { AxiosInstance, AxiosRequestConfig } from 'axios'

import type { AuthProvider } from '../auth'

declare module 'axios' {
	interface AxiosRequestConfig {
		retryGroupId?: string
		needsTokenRefresh?: boolean
	}
}

const AUTH_RETRIES_COUNT = 1

class RetryGroupMetadata {
	readonly lastRetry = Date.now()
	constructor(readonly retriesLeft: number) {}
}

const retriesInProgress = new Map<string, RetryGroupMetadata>()

const generateRandomId = (): string => {
	return Math.random()
		.toString(16)
		.substring(2) // удаляет '0.' из начала строки, оставляя шестнадцатеричное число
}

const getFormattedEndpoint = (config: AxiosRequestConfig): string => {
	const { method, url, baseURL } = config
	const baseURLTrimmed = baseURL?.replace(/\/+$/, '')
	const urlTrimmed = url?.replace(/^\/+/, '')
	return `${method?.toUpperCase()} ${baseURLTrimmed}/${urlTrimmed}`
}

export const http401ErrorHandler = (axios: AxiosInstance, authProvider: AuthProvider): void => {
	axios.interceptors.response.use(
		(response) => {
			const { retryGroupId } = response.config

			if (retryGroupId) {
				logger.info(`Запрос ${getFormattedEndpoint(response.config)} был успешно повторно выполнен после ошибки HTTP 401. ID серии повторений запроса: ${retryGroupId}.`)
				retriesInProgress.delete(retryGroupId)
			}

			return response
		},
		async (error) => {
			const isAuthError = error.response?.status === 401

			if (isAuthError) {
				const retryGroupId = error.config.retryGroupId ??= generateRandomId()
				const retriesLeft = retriesInProgress.get(retryGroupId)?.retriesLeft ?? AUTH_RETRIES_COUNT

				const warningPrefix = `Возникла ошибка HTTP 401 при запросе ${getFormattedEndpoint(error.config)}. ID серии повторений запроса: ${retryGroupId}.`

				if (retriesLeft > 0) {
					logger.warn(`${warningPrefix} Будут выполнены обновление access-токена и повторный запрос. Осталось попыток: ${retriesLeft}.`)

					error.config.needsTokenRefresh = true
					retriesInProgress.set(retryGroupId, new RetryGroupMetadata(retriesLeft - 1))
					return axios.request(error.config)
				}

				retriesInProgress.delete(retryGroupId)
				logger.warn(`${warningPrefix}. Было выполнено повторных запросов: ${AUTH_RETRIES_COUNT}, производится переход на страницу авторизации.`)

				const { logout } = await (await authProvider)()
				logout()
			}

			return Promise.reject(error)
		},
	)
}
